@import '../../media';

.form-container {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 12px;
	margin-bottom: 20px;
	min-height: 60vh;
	border-radius: 25px;
	box-shadow: 7px 8px 12px 0px rgba(0, 0, 0, .33);
	border: 5px solid #fff;
	background: rgba(117, 117, 117, 0.95);
}

.form {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	gap: 17px 0;
	width: 90%;

	&__label {
		padding-bottom: 3px;
		width: 100%;
		opacity: 0.85;
		border-bottom: 3px solid #fff;
		font-size: max(15px, min(2.5vw, 19px));
		font-weight: 500;
		cursor: pointer;
		transition: 0.3s;
		text-align: center;
		user-select: none;

		@include hoverOrActive {
			opacity: 1;
		}
	}

	&__input-wrapper {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		gap: 0 15px;
		margin-bottom: 15px;

	}

	&__input {
		padding: 5px 10px;
		width: 77%;
		height: 40px;
		font-size: max(16px, min(3.3vw, 22px));
		border: 1px solid rgb(128, 128, 128);
		background-color: rgb(255, 255, 255);
		border-radius: 5px;

		@include mobile {
			height: 35px;
		}

		&:focus {
			outline: medium solid rgb(0, 0, 0);
		}


	}

}